@import "../../../Assets/scss/Typography.scss";
@import "../../../Assets/scss/Colors.scss";

.cover-height {
  min-height: 100vh;
}
.cover-heading {
  font-size: 2em;
  text-align: left;
  line-height: 2em;
}
.cover-heading::before {
  content: "\2014 \00A0";
}
.cover-name {
  font-family: $font-dosis;
  font-size: 6em;
  font-weight: 600;
  text-align: left;
  line-height: 0.1em;
}

.cover-title {
  font-family: $font-dosis;
  font-size: 2.5em;
  color: $color-blue;
}
.cover-image {
  display: block;
  width: 100%;
  position: relative;
  margin: auto;
}
.cover-image-mobile {
  display: none;
}
.cover-text {
  font-size: 1.8em;
  line-height: 1.5em;
  font-weight: 500;
  text-align: left;
}

@media screen and (max-width: 599px) {
  .cover-heading {
    font-size: 2em;
    text-align: left;
    line-height: 2em;
  }
  .cover-name {
    font-family: $font-dosis;
    font-size: 3.5em;
    padding-top: 0.5em;
    font-weight: 600;
  }
  .cover-image-mobile {
    display: inline;
    width: 95vw;
  }
  .cover-image {
    display: none;
  }
  .cover-text {
    display: none;
  }
}
