@import "../scss/colors.scss";
@import "../scss/Typography.scss";
@import "../scss/buttons.scss";
// @import "/src/Components/Sections/Cover/Cover.scss"

// Base structure
html,
body {
  color: #fff;
  height: 100%;
  padding: 0;
  margin: 0;
  background-color: $color-background-3;
  font-size: 12px;
}

$grid__bp-md: 1200;
.container {
  max-width: $grid__bp-md * 1px;
  margin: auto;
  padding: 0.5em;
}

.upright-text {
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  text-align: right;
}
@media screen and (max-width: 599px) {
  .upright-text {
    display: none;
    padding-left: 260px;
    writing-mode: none;
    transform: rotate(270deg);
    text-align: center;
  }
}

.color-yellow {
  color: $color-yellow;
}
.color-blue {
  color: $color-blue;
}
.vertical-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.vertical-bottom {
  position: absolute;
  bottom: 0;
}

.blockquote-footer {
  font-family: $font-Open-Sans;
  font-size: 2em;
}

@media screen and (max-width: 599px) {
  .site-wrapper {
    margin: 1em;
  }
  .blockquote-footer {
    font-family: $font-Open-Sans;
    font-size: 1.3em;
  }

  p {
    font-family: $font-Open-Sans;
    font-size: 1.3em;
    line-height: 1.25em;
  }
}

.section-height {
  min-height: 85vh;
}

.text-left {
  text-align: left;
}
.tag-text {
  font-family: $font-dosis;
  font-size: 2.5rem;
  font-weight: 100;
  line-height: 3rem;
  padding: 1em oem;
}
@media screen and (max-width: 599px) {
  .tag-text {
    font-family: $font-dosis;
    font-size: em;
    font-weight: 100;
  }
}

p {
  font-family: $font-Open-Sans;
  display: block;
  margin-block-start: 0.7em;
  margin-block-end: 0.7em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-size: 1.3em;
}

h2 {
  display: block;
  font-size: 1.5em;
  margin-block-start: 0.63em;
  margin-block-end: 0.63em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}
.mobile-only {
  display: none;
}
@media screen and (max-width: 599px) {
  .mobile-only {
    display: inline;
  }
}

.heading {
  font-size: 5em;
  font-weight: 700;
  font-family: $font-dosis;
  padding-bottom: 0.51em;
}
.heading::before {
  content: "\2014 \00A0";
}
.sub-heading {
  font-size: 2em;
  font-weight: 700;
  font-family: $font-dosis;
}

@media screen and (max-width: 599px) {
  .cover-heading {
    line-height: 0em;
  }
}
