@import "../../../Assets/scss/Typography.scss";
@import "../../../Assets/scss/Colors.scss";

.about-image {
  max-width: 400px;
  display: block;
  width: 100%;
  position: relative;
  margin: auto;
}

.about-text {
  font-family: $font-Open-Sans;
  font-size: 1.5rem;
  color: $color-light-blue;
  font-weight: 100;
  line-height: 1.6em;
}
.about-focus {
  font-size: 2rem;
  padding: 10px 0;
}

@media screen and (max-width: 599px) {
  .about-image {
    display: none;
  }
}
